.App{
  min-height: 100vh;
  background: #ecf0f3;
  padding: 1vw;
  color: #555;
  text-align: center;
}

.search-form{
  text-align: center;
}
.search-bar{
  width: 50%;
  border: none;
  box-shadow:
    inset 5px 5px 10px #cbced1,
    inset -5px -5px 10px #ffffff;
  border-radius: 10px;
  padding: 1vh 1vw;
  font-size: 1rem;
  margin-right: 2vw;
}
.search-button{
  padding: 1vh 1vw;
  outline: none;
  border: none;
  cursor: pointer;
  border-radius: 10px;
  font-size: 1rem;
  color: #ffffff;
  text-align: center;
  background-color: #0074E8;
  box-shadow:
    3px 3px 8px #b1b1b1,
    -3px -3px 8px #ffffff;
  transition: 0.5s;
}

.search-button:hover{
  background: #008fe8 ;
}
.search-button:active{
  background: #0060c1 ;
}

.recipes{
  display: inline-grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 2vw;
  grid-auto-rows: minmax(100px, auto);
}

 @media only screen and (max-width: 600px) {
    .recipes{
      display: inline-grid;
      grid-template-columns: repeat(1, 1fr);
      grid-gap: 2vw;
      grid-auto-rows: minmax(100px, auto);
    }
 }
 
 @media only screen and (min-width: 600px) {
  .recipes{
    display: inline-grid;
    grid-template-columns: repeat(1, 1fr);
    grid-gap: 2vw;
    grid-auto-rows: minmax(100px, auto);
  }

 }

 @media only screen and (min-width: 768px) {
  .recipes{
    display: inline-grid;
    grid-template-columns: repeat(1, 1fr);
    grid-gap: 2vw;
    grid-auto-rows: minmax(100px, auto);
  }

 }
 
 @media only screen and (min-width: 998px) {
  .recipes{
    display: inline-grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 2vw;
    grid-auto-rows: minmax(100px, auto);
  }

 }
 
 @media only screen and (min-width: 1200px) {
  .recipes{
    display: inline-grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 2vw;
    grid-auto-rows: minmax(100px, auto);
  }
 } 